.scrolltop-btn {
  position: fixed;
  right: 1.25rem;
  bottom: 1.25rem;
  z-index: 9999;
  width: 52px;
  height: 52px;
  border: none;
  border-radius: 50%;
  background: #111;
  color: #fff;
  display: grid;
  place-items: center;
  box-shadow: 0 10px 24px rgba(0,0,0,.18);
  cursor: pointer;
  opacity: 0;
  transform: translateY(8px) scale(.96);
  pointer-events: none;
  transition: opacity .25s ease, transform .25s ease, background .2s ease;
}
.scrolltop-btn svg { fill: currentColor; }
.scrolltop-btn.is-visible {
  opacity: 1;
  transform: translateY(0) scale(1);
  pointer-events: auto;
}
.scrolltop-btn:hover { background: #f97316; }
.scrolltop-btn:active { transform: translateY(1px) scale(.98); }
@media (min-width: 768px) {
  .scrolltop-btn { width: 60px; height: 60px; }
}