/* ===================== components/common/Footer.css ===================== */

/* Footer container */
.footer {
  background: #111827; /* gray-900 */
  color: #ffffff;
  padding: 4rem 0;
  margin-top: 4rem;
  position: relative;
  overflow: hidden;
}

/* Add subtle background pattern */
.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(circle at 25% 25%, rgba(249, 115, 22, 0.1) 0%, transparent 50%),
                    radial-gradient(circle at 75% 75%, rgba(239, 68, 68, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.footer-container {
  max-width: 80rem; /* max-w-7xl */
  margin: 0 auto;
  padding: 0 1rem;
  position: relative;
  z-index: 1;
}

/* Main grid layout */
.footer-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
}

@media (min-width: 768px) {
  .footer-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

/* Section headings */
.footer h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

/* Contact items container */
.contact-items {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Individual contact item */
.contact-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 0.75rem;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.contact-item:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

/* Contact item content */
.contact-item-content {
  flex: 1;
}

.contact-item .label {
  font-weight: 600;
  color: #ffffff;
  font-size: 0.95rem;
  margin-bottom: 0.25rem;
}

.contact-item .text {
  color: #d1d5db; /* gray-300 */
  font-size: 0.9rem;
  line-height: 1.4;
}

/* Icons styling */
.contact-item .lucide {
  color: #fb923c; /* orange-400 */
  flex-shrink: 0;
  filter: drop-shadow(0 0 8px rgba(251, 146, 60, 0.3));
}

/* Right section description */
.footer-description {
  color: #d1d5db; /* gray-300 */
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

/* Social buttons container */
.social-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

/* Social button styling */
.social-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  border-radius: 9999px;
  font-weight: 600;
  font-size: 0.9rem;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.social-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.social-btn:hover::before {
  left: 100%;
}

.social-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

/* Specific social button colors */
.social-btn--instagram {
  background: linear-gradient(135deg, #f56565, #ed8936);
  color: #fff;
}

.social-btn--instagram:hover {
  background: linear-gradient(135deg, #e53e3e, #dd6b20);
}

.social-btn--facebook {
  background: linear-gradient(135deg, #4299e1, #3182ce);
  color: #fff;
}

.social-btn--facebook:hover {
  background: linear-gradient(135deg, #3182ce, #2c5aa0);
}

.social-btn--whatsapp {
  background: linear-gradient(135deg, #48bb78, #38a169);
  color: #fff;
}

.social-btn--whatsapp:hover {
  background: linear-gradient(135deg, #38a169, #2f855a);
}

/* Bottom section */
.footer-bottom {
  border-top: 1px solid #374151; /* gray-700 */
  margin-top: 3rem;
  padding-top: 2rem;
  text-align: center;
  position: relative;
}

.footer-bottom::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 1px;
  background: linear-gradient(90deg, #f97316, #ef4444);
}

.footer-bottom p {
  color: #9ca3af; /* gray-400 */
  font-size: 0.875rem;
  margin: 0;
  opacity: 0.8;
}

/* Mobile responsiveness */
@media (max-width: 640px) {
  .footer {
    padding: 3rem 0;
    margin-top: 2rem;
  }
  
  .footer-container {
    padding: 0 1rem;
  }
  
  .footer h2 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .contact-item {
    padding: 0.5rem;
  }
  
  .social-buttons {
    justify-content: center;
  }
  
  .social-btn {
    padding: 0.6rem 1rem;
    font-size: 0.85rem;
  }
}

/* Tablet responsiveness */
@media (min-width: 641px) and (max-width: 1023px) {
  .footer {
    padding: 3.5rem 0;
  }
  
  .footer h2 {
    font-size: 1.75rem;
  }
}

/* Animation for contact items */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.contact-item {
  animation: fadeInUp 0.6s ease forwards;
}

.contact-item:nth-child(1) { animation-delay: 0.1s; }
.contact-item:nth-child(2) { animation-delay: 0.2s; }
.contact-item:nth-child(3) { animation-delay: 0.3s; }

/* Social buttons animation */
.social-btn {
  animation: fadeInUp 0.6s ease forwards;
}

.social-btn:nth-child(1) { animation-delay: 0.2s; }
.social-btn:nth-child(2) { animation-delay: 0.3s; }
.social-btn:nth-child(3) { animation-delay: 0.4s; }

/* Accessibility improvements */
.social-btn:focus {
  outline: 2px solid #fff;
  outline-offset: 2px;
}

.contact-item:focus-within {
  outline: 2px solid #fb923c;
  outline-offset: 2px;
}

/* Print styles */
@media print {
  .footer {
    background: transparent !important;
    color: #000 !important;
  }
  
  .social-buttons {
    display: none;
  }
}