/* ---------- Base bar ---------- */
.nav {
  position: sticky;
  top: 0;
  z-index: 50;
  background: linear-gradient(90deg,#f97316,#ef4444,#ec4899);
  box-shadow: 0 8px 24px rgba(0,0,0,.15);
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}
.nav__container {
  max-width: 80rem;
  margin: 0 auto;
  padding: 0 1rem;
}
.nav__row {
  height: 64px;
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
  gap: .75rem;
}
.nav__left {
  display: flex;
  align-items: center;
  gap: 1rem;
  min-width: 0;
}

/* ---------- Logo ---------- */
.logo {
  display: inline-flex;
  align-items: center;
  gap: .5rem;
  font-weight: 800;
  font-size: 1.6rem;
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
}
.logo__emoji { font-size: 1.5rem; }
.logo__pill {
  margin-left: .25rem;
  font-size: .7rem;
  background: #facc15;
  color: #9a3412;
  padding: .2rem .45rem;
  border-radius: 9999px;
  font-weight: 800;
}

/* ---------- Primary Links ---------- */
.nav__links {
  display: none;
  gap: .35rem;
  margin-left: .25rem;
}
@media (min-width:768px) {
  .nav__links { display: inline-flex; }
}
.nav-link {
  display: inline-flex;
  align-items: center;
  gap: .35rem;
  padding: .4rem .65rem;
  border-radius: .6rem;
  color: rgba(255,255,255,.85);
  text-decoration: none;
  font-weight: 600;
  transition: background .18s ease, color .18s ease;
}
.nav-link:hover { background: rgba(255,255,255,.12); color: #fff; }
.nav-link.is-active { background: rgba(255,255,255,.22); color: #fff; }
.nav-link__icon { display: inline-block; }

/* ---------- Right side ---------- */
.nav__right {
  display: flex;
  align-items: center;
  gap: .75rem;
  min-width: 0;
}

/* ---------- Search ---------- */
.search {
  position: relative;
  display: none;
}
@media (min-width:768px) {
  .search { display: block; }
}
.search__input {
  width: 16rem;
  max-width: 100%;
  padding: .55rem .85rem .55rem 2.25rem;
  border-radius: 9999px;
  border: none;
  outline: none;
  background: #fff;
  color: #111827;
  font-weight: 500;
  transition: box-shadow .18s ease;
}
.search__input:focus { box-shadow: 0 0 0 3px rgba(250,204,21,.55); }
.search__icon {
  position: absolute;
  left: .6rem;
  top: 50%;
  transform: translateY(-50%);
  color: #e5e7eb;
}

/* ---------- Icon buttons ---------- */
.icon-btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 38px;
  border-radius: 9999px;
  color: #fff;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: transform .16s ease, background .16s ease, opacity .16s ease;
  flex: 0 0 auto;
}
.icon-btn:hover {
  background: rgba(255,255,255,.12);
  transform: translateY(-1px);
}
.icon-btn--cart {
  background: rgba(124,45,18,.5);
  padding: .35rem;
}
.icon-btn--cart:hover { background: rgba(124,45,18,.65); }

/* Wishlist state highlight */
.icon-btn.has-items { border:1px solid #ef4444; }

/* Count badge */
.icon-btn .badge,
.icon-btn--cart .badge {
  position: absolute;
  top: -6px;
  right: -6px;
  min-width: 20px;
  height: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: .72rem;
  font-weight: 800;
  border-radius: 9999px;
  padding: 0 .35rem;
  box-shadow: 0 6px 16px rgba(0,0,0,.25);
}
.badge--pink { background: #ec4899; color:#fff; }
.badge--red { background: #ef4444; color:#fff; }

/* ---------- Auth buttons ---------- */
.auth { display: inline-flex; gap: .4rem; }
.btn {
  border-radius: 9999px;
  font-weight: 700;
  padding: .45rem .9rem;
  font-size: .95rem;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.btn--light {
  background:#fff; color:#ea580c;
  transition: transform .16s ease, background .16s ease;
}
.btn--light:hover { background:#fff7ed; transform: translateY(-1px); }
.btn--outline {
  color:#fff; border:1.5px solid #fff; background: transparent;
  transition: transform .16s ease, background .16s ease, color .16s ease;
}
.btn--outline:hover { background:#fff; color:#ea580c; transform: translateY(-1px); }

/* ---------- User dropdown ---------- */
.user { position: relative; }
.user__btn {
  display: inline-flex;
  align-items: center;
  gap:.4rem;
  color:#fff;
  background: rgba(255,255,255,.18);
  padding:.35rem .7rem;
  border-radius:9999px;
  border:none;
  cursor:pointer;
}
.user__name { display:none; }
@media (min-width:768px){ .user__name { display:inline; font-weight:600; } }
.user__menu {
  position: absolute;
  right: 0;
  top: 120%;
  min-width: 190px;
  background:#fff;
  color:#111827;
  border-radius:.7rem;
  box-shadow: 0 18px 40px rgba(0,0,0,.18);
  padding:.4rem;
  opacity:0;
  visibility:hidden;
  transform: translateY(-6px);
  transition: opacity .16s ease, transform .16s ease, visibility .16s ease;
}
.user:hover .user__menu { opacity:1; visibility:visible; transform: translateY(0); }
.user__item {
  display:block;
  width:100%;
  text-align:left;
  text-decoration:none;
  color:#374151;
  padding:.55rem .6rem;
  border-radius:.5rem;
  font-weight:600;
  background:transparent;
  border:none;
  cursor:pointer;
}
.user__item:hover { background:#f3f4f6; }
.user__item--danger { color:#dc2626; }
.user__sep { border:none; height:1px; background:#e5e7eb; margin:.35rem .35rem; }

/* ---------- Responsive tweaks ---------- */
@media (min-width:1024px) {
  .nav__container { padding: 0 1.25rem; }
  .search__input { width: 20rem; }
  .nav-link { padding: .48rem .7rem; }
}
@media (min-width:640px) and (max-width:1023px) {
  .logo { font-size: 1.4rem; }
  .logo__pill { display:none; }
  .icon-btn, .icon-btn--cart { width:36px; height:36px; }
  .btn { padding:.4rem .8rem; font-size:.9rem; }
}
@media (max-width:639px) {
  .nav__row {
    height:auto;
    grid-template-columns: 1fr;
    padding:.5rem 0;
    row-gap:.5rem;
  }
  .nav__left {
    justify-content: space-between;
    width: 100%;
  }
  .nav__links { display:none !important; }
  .nav__right {
    width:100%;
    display:grid;
    grid-template-columns: 1fr auto auto auto;
    align-items:center;
    gap:.5rem;
  }
  .search { display:block; grid-column:1 / 2; }
  .search__input {
    width:100%;
    height:38px;
    font-size:.95rem;
  }
  .icon-btn, .icon-btn--cart {
    width:36px;
    height:36px;
    justify-self:end;
  }
  .auth {
    justify-self:end;
    gap:.35rem;
  }
  .btn { padding:.35rem .65rem; font-size:.88rem; }
  .btn--outline { border-width:1.25px; }
  .user__btn { padding:.3rem .6rem; }
}
@media (max-width:360px) {
  .logo { font-size:1.35rem; }
  .icon-btn, .icon-btn--cart { width:34px; height:34px; }
  .btn { padding:.3rem .55rem; font-size:.82rem; }
}
