/* ========== Animated Login Screen ========== */

:root {
  --brand1: #fb923c; /* orange-400 */
  --brand2: #ef4444; /* red-500   */
  --bg:     #0b1020; /* deep navy gradient base */
}

/* Full screen wrapper */
.login-wrapper {
  position: relative;
  min-height: 100vh;
  padding: 32px 16px;
  display: grid;
  place-items: center;
  overflow: hidden;
  background:
    radial-gradient(1200px 600px at 10% 0%, rgba(255,255,255,0.06), transparent 60%),
    radial-gradient(800px 400px at 90% 10%, rgba(255,255,255,0.05), transparent 60%),
    linear-gradient(180deg, #0e132b 0%, #0b1020 100%);
}

/* Soft grain + sheen */
.login-wrapper::before {
  content: "";
  position: absolute;
  inset: -10%;
  background: radial-gradient(1200px 600px at 50% -10%, rgba(251,146,60,0.08), transparent 60%);
  pointer-events: none;
}
.login-card .auth-input {
  background: rgba(255,255,255,0.9);
  border-radius: 12px;
  border: 1px solid rgba(17,24,39,.12);
}

/* Card */
.login-card {
  position: relative;
  width: 100%;
  max-width: 420px;
  padding: 28px;
  border-radius: 20px;
  background: rgba(255,255,255,0.06);
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
  box-shadow: 0 20px 60px rgba(0,0,0,0.35), inset 0 0 0 1px rgba(255,255,255,0.08);
  color: #fff;
  z-index: 2;
  animation: cardIn 500ms cubic-bezier(.21,.98,.6,.99) both;
}

@keyframes cardIn {
  from { transform: translateY(12px) scale(.98); opacity: 0; }
  to   { transform: translateY(0)    scale(1);    opacity: 1; }
}

.login-title {
  margin: 0 0 18px;
  font-weight: 900;
  letter-spacing: -0.02em;
  font-size: clamp(24px, 3vw, 30px);
  background: linear-gradient(90deg, var(--brand1), var(--brand2));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* Form elements inside the card still use your Tailwind classes.
   This CSS only polishes default focus states when Tailwind isn't applied. */
.login-card input[type="email"],
.login-card input[type="password"] {
  background: rgba(255,255,255,0.9);
  color: #111827;
  border: 1px solid rgba(17,24,39,.12);
  border-radius: 12px;
}
.login-card input:focus {
  outline: none;
  border-color: #fdba74;
  box-shadow: 0 0 0 3px rgba(251,146,60,.25);
}

/* Floating food emojis */
.floating-food {
  position: absolute;
  font-size: clamp(22px, 3.2vw, 32px);
  opacity: .75;
  filter: drop-shadow(0 6px 12px rgba(0,0,0,.25));
  animation: floatY 10s ease-in-out infinite, swayX 8s ease-in-out infinite;
  z-index: 1;
}

/* Randomize each via nth-child (positions and delays) */
.floating-food:nth-of-type(1) { top: 6%;   left: 10%; animation-delay: .0s, .0s; }
.floating-food:nth-of-type(2) { top: 12%;  right: 12%; animation-delay: .8s, .2s; }
.floating-food:nth-of-type(3) { bottom: 14%; left: 8%;  animation-delay: 1.4s, .4s; }
.floating-food:nth-of-type(4) { bottom: 10%; right: 10%; animation-delay: 2.0s, .6s; }
.floating-food:nth-of-type(5) { top: 45%;   left: 4%;  animation-delay: 2.6s, .8s; }
.floating-food:nth-of-type(6) { top: 30%;   right: 6%; animation-delay: 3.2s, 1.0s; }
.floating-food:nth-of-type(7) { bottom: 24%; left: 16%; animation-delay: 3.8s, 1.2s; }
.floating-food:nth-of-type(8) { bottom: 28%; right: 16%; animation-delay: 4.4s, 1.4s; }

@keyframes floatY {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50%      { transform: translateY(-16px) rotate(4deg); }
}
@keyframes swayX {
  0%, 100% { transform: translateX(0px); }
  50%      { transform: translateX(10px); }
}

/* Responsive refinements */
@media (max-width: 640px) {
  .login-card { padding: 22px; border-radius: 16px; }
  .floating-food { opacity: .65; }
}

/* Optional dark fine-tuning if system dark is on */
@media (prefers-color-scheme: dark) {
  .login-wrapper { background: linear-gradient(180deg, #0d1327 0%, #0a0f1e 100%); }
}
.login-card .auth-input {
  background: rgba(255,255,255,0.92);
  color: #111827;
  border: 1px solid rgba(17,24,39,.12);
  border-radius: 12px;
}
.login-card .auth-input:focus {
  border-color: #fdba74;
  box-shadow: 0 0 0 3px rgba(251,146,60,.25);
}
